@import url("https://use.typekit.net/uwe6nvz.css");
::-webkit-scrollbar {
  display: none;
}
html {
  scroll-behavior: smooth;
}
body,
h1 {
  margin: 0;
}
body {
  overflow-x: hidden;
  background-color: ghostwhite;
  scroll-behavior: smooth;
}

* {
  font-family: semplicitapro, sans-serif;
  font-weight: 400;
}

.navbar {
  background-color: rgb(17, 107, 157);
  font-weight: 300;
  font-size: 16px;
  letter-spacing: 1px;
  text-align: right;
  width: 100%;
  height: 7vh;
  background: url("./assets/background.svg") no-repeat fixed;
  position: fixed;
  z-index: 1;
}
a {
  margin-right: 25px;
  position: relative;
  top: 20px;
  color: rgb(248, 248, 255);
  text-decoration: none;
}
.Landing {
  background: url("./assets/background.svg") no-repeat fixed;
  background-size: cover;
  height: 100vh;
  display: flex;
  flex-direction: column;
  .Name {
    color: rgb(248, 248, 255);
    font-weight: 500;
    font-size: 50px;
    letter-spacing: 3px;
    position: relative;
    text-align: center;
    margin-top: 40vh;
  }
  .Discription {
    color: rgb(223, 223, 229);
    font-size: 16px;
    letter-spacing: 2px;
    position: relative;
    text-align: center;
    height: 200px;
  }
  .button_div {
    margin: 10vh auto;
    .scroll_button {
      background-color: rgba(0, 0, 0, 0.05);
      border: 1;
      color: rgb(223, 223, 229);
      text-align: center;
      text-decoration: none;
      display: inline-block;
      font-size: 14px;
      border-radius: 100px;
      width: 250px;
      height: 50px;
      outline: none;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      &:hover {
        background-color: rgba(0, 0, 0, 0.08);
      }
    }
  }
}

.Education,
.Experience {
  background-color: ghostwhite;
  display: flex;
  flex-direction: column;
  text-align: center;

  .hard_header {
    margin: 5vh auto;
  }
  h2 {
    font-size: 32px;
    letter-spacing: 3px;
    font-weight: 500;
  }

  .edu_content {
    height: 90%;
    display: flex;
    flex-direction: column;
  }
  .content_row {
    display: flex;
    flex-direction: row;
  }
  .edu_time {
    background-color: ghostwhite;
    width: 41vw;
    margin-left: 30px;
  }
  .edu {
    width: 49vw;
    padding-bottom: 40px;
  }
  .content {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 50%;
  }
  .border {
    border-left: 3px solid;
    border-image: linear-gradient(to bottom, #0064a6, rgba(0, 0, 0, 0)) 1 100%;
  }
  .dot {
    height: 10px;
    width: 10px;
    background-color: #08456d;
    border-radius: 50%;
    margin-left: -7px;
    margin-top: -5px;
  }
  h3.left {
    font-size: 16px;
    letter-spacing: 2px;
    width: 90%;
    text-align: right;
    margin-bottom: 20px;
    margin-top: -2px;
  }

  h4.left {
    font-size: 14px;
    letter-spacing: 1px;
    width: 90%;
    text-align: right;
    font-weight: 700;
  }

  h3.right {
    font-size: 16px;
    letter-spacing: 2px;
    width: 80%;
    text-align: left;
    margin-bottom: 20px;
    margin-top: -12px;
    margin-left: 20px;
    font-weight: 700;
  }

  h4.right {
    font-size: 14px;
    letter-spacing: 1px;
    width: 80%;
    text-align: left;
    line-height: 24px;
    margin: 10px 10px 10px 20px;
  }
}
.gradient {
  text-transform: uppercase;
  background: linear-gradient(to right, #2702c9 0%, #330867 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.arrow {
  margin: 0 auto;
  animation: move 1.5s ease-in-out infinite;
  height: 100px;
}

.landing_desc {
  margin-bottom: 40px;
}

@keyframes move {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 10px);
  }
  100% {
    transform: translate(0, 0px);
  }
}

.Skills {
  background-image: linear-gradient(#0064a6, #009996);
  padding-top: 5vh;
  height: 93vh;

  .emphasis_header {
    color: rgba(248, 248, 255, 1);
    font-weight: 500;
    text-decoration: underline;
  }

  .emphasis {
    color: rgba(248, 248, 255, 1);
    font-weight: 500;
  }
  .skill_header {
    margin: 5vh auto;
    & h2 {
      color: white;
      font-size: 32px;
      letter-spacing: 3px;
      font-weight: 500;
    }
  }
  .grid_header {
    width: 50%;
    margin: 0 auto;
    margin-bottom: 60px;
    display: grid;
    grid-template-areas: "science_header deep_header data_header dev_header";
    grid-gap: 10px;
    color: rgba(248, 248, 255, 0.5);
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    .science_header {
      grid-area: science_header;
      transition: all 0.3s ease-in-out;
    }
    .deep_header {
      grid-area: deep_header;
      transition: all 0.3s ease-in-out;
    }
    .data_header {
      grid-area: data_header;
      transition: all 0.3s ease-in-out;
    }
    .dev_header {
      grid-area: dev_header;
      transition: all 0.3s ease-in-out;
    }
  }

  .grid_table {
    width: 80%;
    height: 60%;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
      "deep sql html geo"
      "git python keras net"
      "r tensor hadoop liner"
      "recurrent js baysian scala"
      "forcast rein css transformer"
      "node time arch react"
      "nlp spark tableau image";
    grid-gap: 10px;
    transition: all 0.3s ease-in-out;

    color: rgba(248, 248, 255, 0.5);
    .deep {
      grid-area: deep;
      transition: all 0.3s ease-in-out;
    }
    .sql {
      grid-area: sql;
      transition: all 0.3s ease-in-out;
    }
    .html {
      grid-area: html;
      transition: all 0.3s ease-in-out;
    }
    .geo {
      grid-area: geo;
      transition: all 0.3s ease-in-out;
    }

    .git {
      grid-area: git;
      transition: all 0.3s ease-in-out;
    }
    .python {
      grid-area: python;
      transition: all 0.3s ease-in-out;
    }
    .keras {
      grid-area: keras;
      transition: all 0.3s ease-in-out;
    }
    .net {
      grid-area: net;
      transition: all 0.3s ease-in-out;
    }

    .r {
      grid-area: r;
      transition: all 0.3s ease-in-out;
    }
    .tensor {
      grid-area: tensor;
      transition: all 0.3s ease-in-out;
    }
    .hadoop {
      grid-area: hadoop;
      transition: all 0.3s ease-in-out;
    }
    .liner {
      grid-area: liner;
      transition: all 0.3s ease-in-out;
    }

    .recurrent {
      grid-area: recurrent;
      transition: all 0.3s ease-in-out;
    }
    .js {
      grid-area: js;
      transition: all 0.3s ease-in-out;
    }
    .baysian {
      grid-area: baysian;
      transition: all 0.3s ease-in-out;
    }
    .scala {
      grid-area: scala;
      transition: all 0.3s ease-in-out;
    }

    .forcast {
      grid-area: forcast;
      transition: all 0.3s ease-in-out;
    }
    .rein {
      grid-area: rein;
      transition: all 0.3s ease-in-out;
    }
    .css {
      grid-area: css;
      transition: all 0.3s ease-in-out;
    }
    .transformer {
      grid-area: transformer;
      transition: all 0.3s ease-in-out;
    }

    .node {
      grid-area: node;
