.space {
  padding-top: 20px;
}
.narrow {
  height: 25%;
}
.Projects {
  background-color: white;
  padding-top: 5vh;
}
.Contact {
  height: 90vh;
}
.contact_left {
  background-color: ghostwhite;
  width: 50vw;
  margin-left: 30px;
}
h3.contact_name {
  font-size: 28px;
  letter-spacing: 2px;
  width: 90%;
  text-align: right;
  margin-bottom: 20px;
  margin-top: -2px;
}
h4.contact_name {
  font-size: 16px;
  letter-spacing: 2px;
  width: 90%;
  text-align: right;
  margin-bottom: 20px;
  margin-top: -2px;
}
.contact_content {
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact_right {
  width: 50vw;
  padding-bottom: 40px;
}
h4.contact_detail {
  font-size: 14px;
  letter-spacing: 1px;
  width: 80%;
  text-align: left;
  line-height: 24px;
  margin: 10px 10px 10px 20px;
  & a {
    top: 0;
    color: black;
    text-decoration: underline;
  }
}
.Projects {
  background-color: ghostwhite;
}
.icons {
  display: flex;
  width: 60%;
  justify-content: left;
  margin-left: 20px;
}
